@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  box-sizing: border-box;
}

body {
  background: #fff;
}

h1 {
  padding: 0;
  margin: 0;
}

.bg-container {
  margin: 96px auto;
  max-width: 1400px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 96px;
}
@media only screen and (max-width: 990px) {
  .bg-container {
    gap: 24px !important;
    padding: 5vw;
  }
}
.bg-container .sm-container {
  max-width: 505px;
  max-height: 330px;
}
.bg-container .image-hero img {
  width: 100%;
  height: 100%;
}
.bg-container .content {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
@media only screen and (max-width: 990px) {
  .bg-container .content {
    text-align: center;
    align-items: center;
  }
  .bg-container .content > .section {
    display: flex !important;
    flex-direction: column;
  }
  .bg-container .content > .section .title {
    align-self: center !important;
  }
}
.bg-container .content .section {
  display: grid;
  gap: 24px;
  grid-template-rows: repeat(2, 1fr);
}
.bg-container .content .section .title {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  align-self: flex-end;
}
.bg-container .content .section .text {
  max-width: 380px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.bg-container .content > #btn-counter {
  background: #e4c1ae;
  width: 128px;
  height: 48px;
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  border: 5px solid transparent;
}
.bg-container .content > #btn-counter:hover {
  cursor: pointer;
  border-color: rgb(111, 216, 230);
}

.layout {
  left: 0;
  top: 0;
  position: fixed;
  background: rgba(12, 12, 12, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.layout .alert-container {
  background: #fff;
  width: 920px;
  min-height: 210px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media only screen and (max-width: 720px) {
  .layout .alert-container {
    padding: 5vw;
    gap: 2vh;
  }
}
.layout .alert-container .left-side {
  padding: 56px 95px 56px 95px;
  display: grid;
  gap: 24px;
  grid-template-rows: repeat(2, 1fr);
}
@media only screen and (max-width: 720px) {
  .layout .alert-container .left-side {
    padding: 0;
    gap: 2vh;
  }
  .layout .alert-container .left-side .title {
    font-size: clamp(2.2rem, 100%, 42px) !important;
  }
  .layout .alert-container .left-side .text {
    font-size: clamp(1.2rem, 100%, 16px) !important;
  }
}
@media only screen and (max-width: 420px) {
  .layout .alert-container .left-side {
    display: flex;
    flex-direction: column;
  }
}
.layout .alert-container .left-side .title {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
}
.layout .alert-container .left-side .text {
  max-width: 380px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.layout .alert-container .left-side .text .counter {
  font-weight: 600;
}
.layout .alert-container .right-side {
  display: grid;
  gap: 24px;
  grid-template-rows: repeat(2, 1fr);
  padding: 46px;
}
@media only screen and (max-width: 720px) {
  .layout .alert-container .right-side {
    padding: 0;
  }
}
.layout .alert-container .right-side #btn-close {
  width: 12px;
  height: 12px;
  position: relative;
  justify-self: flex-end;
  border: none;
  background: none;
}
.layout .alert-container .right-side #btn-close:hover {
  cursor: pointer;
  background-color: red;
}
.layout .alert-container .right-side #btn-close:after {
  content: "";
  height: 12px;
  border-left: 2px solid rgb(0, 0, 0);
  position: absolute;
  transform: rotate(45deg);
  top: 0;
}
.layout .alert-container .right-side #btn-close:before {
  content: "";
  height: 12px;
  border-left: 2px solid rgb(0, 0, 0);
  position: absolute;
  transform: rotate(-45deg);
  top: 0;
}
.layout .alert-container .right-side > #btn-reset {
  background: #e4c1ae;
  width: 128px;
  height: 48px;
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  border: 5px solid transparent;
  display: none;
}
.layout .alert-container .right-side > #btn-reset:hover {
  cursor: pointer;
  border-color: rgb(111, 216, 230);
}
@media only screen and (max-width: 720px) {
  .layout .alert-container .right-side > #btn-reset {
    transform: scale(0.6);
  }
}